// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import store from 'common/js/store.js'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import VueJsonp from 'vue-jsonp'
import VueLazyLoad from 'vue-lazyload'
import cameraman from './cameraman.vue'
import cameramanComment from 'components/cameramanComment/cameramanComment.vue'
import works from 'components/works/works.vue'
import briefIntroduction from 'components/briefIntroduction/briefIntroduction.vue'
import scheduleDate from 'components/scheduleDate/scheduleDate.vue'
import   "common/stylus/index.styl"
import   "common/css/reset.css"
import   "common/css/animate.css"
//使用路由
Vue.use(VueRouter)
Vue.use(VueResource)
Vue.use(VueJsonp)
Vue.use(VueLazyLoad)
const routes = [
  {path: '*', component: works},
  {path: '/works/:id/:typeId', component: works},
  {path: '/briefIntroduction/:id/:typeId', component: briefIntroduction},
  {path: '/cameramanComment/:id/:typeId', component: cameramanComment},
  {path: '/scheduleDate/:id/:typeId', component: scheduleDate}
]
// 创建路由
const router = new VueRouter({
  routes
});

// 4. 创建和挂载根实例。
// 记得要通过 router 配置参数注入路由，
// 从而让整个应用都有路由功能
new Vue({
  template: '<cameraman/>',
  components: {cameraman},
  store,
  router,
  data: {
    eventHub: new Vue() // 用于子控件向父控件传递事件  radio需要
  }
}).$mount('#app')



